





































import {
  computed, defineComponent, onMounted, onUnmounted, PropType, ref,
} from '@vue/composition-api';
import { useWindowScrollSettings } from '@/app/shared/client/logic/scroll-settings';
import { useTileOffsetSettings } from '@/app/shared/client/logic/offset-settings';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import {
  StyleKeyValue,
  tileStyleSelector,
  generalStyles,
  textStyles,
} from '@/app/website/client/logic/tile-style';
import { checkIfTileTransparent } from '@/app/website/client/logic/tile-properties';
import {
  TextTileContent,
  TextTileDesign,
  TextTileLayout,
} from '@/app/shared/client/type/tile/text-tile';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import {
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { ViewportSettings } from '@/app/shared/client/type/settings/viewport-settings';
import { checkIfTileShown } from '@/app/website/client/logic/appearance-behaviour';
import TitleLeftDefaults from '@/app/website/client/defaults/text/title-left.json';
import SubtitleRightDefaults from '@/app/website/client/defaults/text/subtitle-right.json';
import OneColumnDefaults from '@/app/website/client/defaults/text/one-column.json';
import TwoColumnsDefaults from '@/app/website/client/defaults/text/two-columns.json';
import CenterDefaults from '@/app/website/client/defaults/text/center.json';
import DescriptionRightDefaults from '@/app/website/client/defaults/text/description-right.json';
import { TextTileLayoutDefaults } from '@/app/shared/client/type/defaults/text-tile-defaults';
import FontLoader from '@/app/website/client/view/component/FontLoader.vue';
import TileTitle from '@/app/website/client/view/component/TileTitle.vue';

interface TextTileProps {
  content: TextTileContent;
  design: TextTileDesign;
  viewportSettings: ViewportSettings;
}

function getDefaults(layout: TextTileLayout | undefined): TextTileLayoutDefaults | undefined {
  switch (layout) {
    case 'TITLE_LEFT':
      return TitleLeftDefaults as TextTileLayoutDefaults;
    case 'SUBTITLE_RIGHT':
      return SubtitleRightDefaults as TextTileLayoutDefaults;
    case 'ONE_COLUMN':
      return OneColumnDefaults as TextTileLayoutDefaults;
    case 'TWO_COLUMNS':
      return TwoColumnsDefaults as TextTileLayoutDefaults;
    case 'CENTER':
      return CenterDefaults as TextTileLayoutDefaults;
    case 'DESCRIPTION_RIGHT':
      return DescriptionRightDefaults as TextTileLayoutDefaults;
    default:
      return undefined;
  }
}

function computedLayoutClass(layout: TextTileLayout | undefined): string {
  switch (layout) {
    case 'TITLE_LEFT':
      return 'title-left';
    case 'SUBTITLE_RIGHT':
      return 'subtitle-right';
    case 'ONE_COLUMN':
      return 'one-column';
    case 'TWO_COLUMNS':
      return 'two-columns';
    case 'CENTER':
      return 'center';
    case 'DESCRIPTION_RIGHT':
      return 'description-right';
    default:
      return 'title-left';
  }
}

function tileStyles(
  design: TextTileDesign,
  layoutHasSubtitle: boolean,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [
    ...generalStyles(design.general),
    ...textStyles('title', design.title),
    ...textStyles('description', design.description),
  ];

  if (layoutHasSubtitle) {
    styles = [
      ...styles,
      ...textStyles('subtitle', design.subtitle),
    ];
  }

  return styles;
}

function layoutWithSubtitle(
  layout: TextTileLayout | undefined,
): boolean {
  return layout === 'ONE_COLUMN'
    || layout === 'TITLE_LEFT'
    || layout === 'SUBTITLE_RIGHT'
    || layout === 'TWO_COLUMNS'
    || layout === 'CENTER';
}

const TextTile = defineComponent({
  components: {
    FontLoader,
    TileTitle,
  },
  props: {
    content: {
      required: true,
      type: Object as PropType<TextTileContent>,
    },
    design: {
      required: true,
      type: Object as PropType<TextTileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
    isHeadTile: {
      required: true,
      type: Boolean,
    },
  },
  setup(props: TextTileProps, { attrs, root, emit }) {
    const tileRef = ref<HTMLElement>();

    const {
      windowScrollTop,
      subscribeOnWindowScrollChange,
    } = useWindowScrollSettings();

    const {
      tileOffsetTop,
      tileOffsetHeight,
      subscribeOnTileOffsetChange,
    } = useTileOffsetSettings(tileRef);

    const layoutClass = computed(() => `ins-tile--${computedLayoutClass(props.design.layout)}`);
    const defaults = computed(() => getDefaults(props.design.layout));

    const design = computed<TextTileDesign>(() => {
      const userValue = props.design;
      const general: GeneralDesignSettings = {
        ...defaults.value?.general,
        ...userValue.general,
        background: {
          ...defaults.value?.general?.background,
          ...userValue.general?.background,
          solid: {
            ...defaults.value?.general?.background?.solid,
            ...userValue.general?.background?.solid,
          },
          gradient: {
            ...defaults.value?.general?.background?.gradient,
            ...userValue.general?.background?.gradient,
          },
        },
      };

      const title: TextSettings = {
        ...(defaults.value?.title as TextSettings),
        ...userValue.title,
      };

      const subtitle: TextSettings = {
        ...(defaults.value?.subtitle as TextSettings),
        ...userValue.subtitle,
      };

      const description: TextSettings = {
        ...(defaults.value?.description as TextSettings),
        ...userValue.description,
      };

      return {
        layout: userValue.layout,
        general,
        title,
        subtitle,
        description,
      };
    });

    const isTileShowRef = ref(false);
    const isTileShown = computed(() => checkIfTileShown(
      isTileShowRef,
      props.viewportSettings,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const fonts = computed(() => [
      design.value.title?.font,
      design.value.subtitle?.font,
      design.value.description?.font,
    ]);

    const layoutHasSubtitle = computed(() => layoutWithSubtitle(design.value.layout));

    const styleSelector = computed(() => {
      const styles = tileStyles(
        design.value,
        layoutHasSubtitle.value,
      );
      return tileStyleSelector(`#${attrs.id}`, styles);
    });
    const hasTitle = computed(() => {
      const isTitleVisible = design.value.title?.visible;
      const hasTitleContent = isNotNullOrEmpty(props.content.title);
      return isTitleVisible && hasTitleContent;
    });
    const hasSubtitle = computed(() => {
      const isSubtitleVisible = design.value.subtitle?.visible;
      const hasSubtitleContent = isNotNullOrEmpty(props.content.subtitle);
      return isSubtitleVisible && hasSubtitleContent;
    });
    const hasDescription = computed(() => {
      const isDescriptionVisible = design.value.description?.visible;
      const hasDescriptionContent = isNotNullOrEmpty(props.content.description);
      return isDescriptionVisible && hasDescriptionContent;
    });
    const hasTransparentBackground = computed(() => checkIfTileTransparent(
      design.value?.general,
      root.$store.state.siteSettings?.design,
    ));

    onMounted(() => {
      emit('tile-loaded');
      subscribeOnWindowScrollChange();
      subscribeOnTileOffsetChange();
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      tileRef,
      layoutClass,
      fonts,
      styleSelector,
      hasTitle,
      hasSubtitle,
      layoutHasSubtitle,
      hasDescription,
      hasTransparentBackground,
      isTileShown,
    };
  },
});

export default TextTile;
